.index-page{
  position: relative;


  .owl-stage{
    display: flex;
  }
  .banner-block{
    background-image:url(../images/fon.jpg);
    background-repeat: no-repeat;
    background-position: center;
    height: 501px;
    position: relative;
    span{
      &.blue{
        font-size: 32px;
        line-height: 36px;
        color: #0e569e;
        font-weight: bold;
        width: 460px;
        display: block;
        margin-left: 153px;
        padding-top: 66px;
        padding-bottom: 22px;
      }
      &.orange{
        font-size: 36px;
        line-height: 40px;
        color: #ff6600;
        display: block;
        font-weight: bold;
        width: 230px;
        margin-bottom: 24px;
      }
      &.litle-blue{
        font-size: 18px;
        line-height: 21px;
        color: #0e569e;
        display: block;
      }
    }
    .wight-block{
      background-color:#fff;
      box-sizing: border-box;
      padding: 40px 49px 30px 153px;
      width: 650px;
      display: flex;
      position: relative;
      justify-content: space-between;
      align-items: center;
      &:before{
        content:'';
        display: block;
        background-image:url(../images/men.png);
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        width: 564px;
        height: 472px;
        position: absolute;
        right: -564px;
        top: -50%;
        margin-top: -37px;
      }
      &-2{
        &:before{
          background-image:url(../images/men2.png);
          width: 507px;
          height: 455px;
          margin-top: -20px;
        }
      }
      &-3{
        &:before{
          background-image:url(../images/men3.png);
          width: 453px;
          height: 476px;
          margin-top: -53px;
        }
      }
    }
    .item{
      margin-bottom: 55px;
    }
    .item2{
      span.litle-blue{
        width: 275px;
      }
    }
    .item3{
      span.litle-blue{
        width: 275px;
      }
    }
    .owl-nav{
      display: none;
    }
    .owl-dots{
      margin-left: 150px;
      margin-top: -40px;
      .owl-dot{
        width: 10px;
        height: 10px;
        border-radius: 5px;
        background-color:#fff;
        border: none;
        padding: 0px;
        display: inline-block;
        margin-right: 14px;
        position: relative;
        &.active{
          span{
            width: 8px;
            height: 8px;
            background-color:#0e569e;
            display: block;
            position: absolute;
            left: 50%;
            top: 50%;
            margin-top: -4px;
            margin-left: -4px;
            border-radius: 4px;
          }
        }
      }
    }
    .slider_clients{
      height: 100%;
    }
  }
  .services{
    background-color:#fff;
    position: relative;
    &:before{
      content:'';
      width: 40px;
      height: 21px;
      display: block;
      position: absolute;
      left: 50%;
      margin-left: -20px;
      background-image:url(../img/arrow2.png);
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;
      z-index: 100;
      top: -1px;
    }
    &:after{
      content:'';
      display: block;
      position: absolute;
      width: 40px;
      height: 20px;
      background-image:url(../img/arrow3.png);
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;
      z-index: 100;
      bottom:-19px;
      left: 50%;
      margin-left: -20px;
    }
    .inner{
      box-sizing: border-box;
      background-color:#0E569E;
      padding-left: 150px;
      padding-right: 150px;
      h2{
        text-align: center;
        color: #fff;
        font-size: 32px;
        text-transform: uppercase;
        padding-top: 66px;
        padding-bottom: 50px;
        font-weight: 400;
        margin: 0;
      }
    }
    .flex{
      display: flex;
      flex-wrap: wrap;
      padding-bottom: 37px;
      .item{
        box-sizing: border-box;
        padding: 30px 29px 34px;
        background-color:#fff;
        border:1px solid #0e569e;
        margin-right: 41px;
        width: calc(33.3% - 41px);
        margin-bottom: 41px;
        &:nth-child(1){
          p{padding-bottom: 43px;}
        }
        &:nth-child(2){
          p{padding-bottom: 43px;}
        }
        &:nth-child(5){
          p{padding-left: 20%;padding-right: 20%;}
        }
        &:nth-child(8){
          p{padding-left: 20%;padding-right: 20%;}
        }
        &:nth-child(3n){
          margin-right: 0px;
        }
        &:hover{
          border-color: #FF6600;
        }
        .icon{
          margin: 0 auto;
          background-image:url(../img/icons.png);
          background-repeat: no-repeat;
        }
        p{
          font-size: 20px;
          text-align: center;
          color: #0e569e;
          margin: 0;
          padding-top: 29px;
          padding-bottom: 20px;
          display: block;
          border-bottom: 1px solid #DDE7F1;
        }
        ul{
          padding-left: 16px;
          letter-spacing: -0.8px;
          li{
            margin-top: 26px;
            position: relative;
            &:before{
              content:'';
              display: block;
              position: absolute;
              width: 5px;
              height: 5px;
              background-color:#ff6600;
              border-radius: 2.5px;
              left: -15px;
              top: 7px;
            }
            a{
              color: #0e569e;
              &:hover{
                color: #ff6600;
              }
            }
          }
        }
        &:nth-child(1){
          .icon{
            height: 52px;
            width: 50px;
          }
        }
        &:nth-child(2){
          .icon{
            height: 53px;
            width: 73px;
            background-position: 49% top;
          }
        }
        &:nth-child(3){
          .icon{
            height: 54px;
            width: 77px;
            background-position: right top;
          }
        }
        &:nth-child(4){
          .icon{
            height: 54px;
            width: 54px;
            background-position: center left;
          }
        }
        &:nth-child(5){
          .icon{
            height: 52px;
            width: 50px;
            background-position: 49% 50%;
          }
        }
        &:nth-child(6){
          .icon{
            height: 52px;
            width: 52px;
            background-position: 98.2% center;
          }
        }
        &:nth-child(7){
          .icon{
            height: 52px;
            width: 52px;
            background-position: left bottom;
          }
        }
        &:nth-child(8){
          .icon{
            height: 55px;
            width: 56px;
            background-position: 49% bottom;
          }
        }
        &:nth-child(9){
          .icon{
            height: 52px;
            width: 52px;
            background-position: 98.2% 99.8%;
          }
        }
        .button{
          margin: 0 auto;
          margin-top: 49px;
        }
      }
    }
  }
  .about_company{
    padding-top: 19px;
    .inner{
      box-sizing: border-box;
      padding-left: 150px;
      padding-right: 150px;
    }
    .flex{
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      .item{
        height: 300px;
        width: 47%;
        position: relative;
        margin-top: 46px;
        .blue,.orange{
          font-size: 32px;
          display: block;
        }
        .blue{
          color: #0e569e;
        }
        .orange{
          color: #ff6600;
        }
        p{
          padding-right: 10%;
        }
        &.image{
          display: flex;
          justify-content: flex-end;
          align-items: flex-end;
          background-image:url(../images/pix.png);
          background-repeat: no-repeat;
        }
        &:nth-child(2){
          background-position: right top;
        }
        &:nth-child(3){
          background-position: 0 33.5%;
        }
        &:nth-child(6){
          background-position: 100% 66.5%;
          .wight{
            padding-right: 10%;
          }
        }
        &:nth-child(7){
          background-position: 0 100%;
        }
        .wight{
          width: 218px;
          height: 79px;
          background-color:#fff;
          padding: 17px 20px;
          box-sizing: border-box;
          font-size: 14px;
          display: flex;
          align-items: center;
        }
      }
    }
  }
  .description{
    margin-top: 125px;
    .flex{
      width: 100%;
      display: flex;
      justify-content: space-around;
      .item{
        &:nth-child(3){
          span{
            padding:0 10%;
            box-sizing:border-box;
            text-align: center;
          }
        }
        .icon{
          margin: 0 auto;
            background-image:url(../img/icons2.png);
            background-repeat: no-repeat;
            &1{
              height: 75px;
              width: 73px;
              background-position: left center;
            }
            &2{
              height: 75px;
              width: 101px;
              background-position: 32% center;
            }
            &3{
              height: 76px;
              width: 51px;
              background-position: 65% center;
            }
            &4{
              height: 75px;
              width: 102px;
              background-position: 100% center;
            }
        }
        span{
          margin-top: 36px;
          display: block;
          font-size: 18px;
          color: #0e569e;
          font-weight: bold;
        }
      }
    }
  }
  .banner_block-2{
    position: relative;
    &:before{
      content:'';
      width: 40px;
      height: 21px;
      background-image: url(../img/arrow2.png);
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;
      position: absolute;
      left: 50%;
      margin-left: -20px;
      bottom:-20px;
      z-index: 10;
    }
    .slider_clients-2{
      background-image:url(../images/pix2.jpg);
    background-position:center;
    background-repeat:no-repeat;
    background-size:cover;
    }
    .item{
      display: flex;
      justify-content: space-between;
      .left-block{
        max-width: 640px;
        background-color: #fff;
        opacity: 0.9;
        box-sizing:border-box;
        padding: 46px 37px 56px 149px;
        margin-bottom: 62px;
        margin-top: 239px;
        span{
          display: block;
            &:nth-child(1){
              font-size: 32px;
              margin-bottom: 25px;
              text-transform: uppercase;
              color:#0e569e;
            }
            &:nth-child(2),&:nth-child(3){
              font-size: 24px;
              color:#0e569e;
            }
            &:nth-child(4){
              font-size: 24px;
              color:#ff6600;
              margin-bottom: 24px;
            }
            &:nth-child(5){
              font-weight: bold;
            }
        }
      }
      .right-block{
        align-self: flex-end;
        a{
          margin-bottom: 62px;
          margin-right: 149px;
          display: block;
          text-decoration: none;
          font-family: 'Conv_GOTHAPROBLA_0', sans-serif;
          font-weight: bold;
          font-size: 14px;
          color: #fff;
          width: 180px;
          height: 40px;
          line-height: 40px;
          text-align: center;
          background-color: #FF6600;
          &:hover{
            background-color:#FF9000;
          }
        }
      }
    }
    .owl-nav{
      display: none;
    }
    .owl-dots{
      margin-left: 150px;
      position: absolute;
      bottom:22px;
      .owl-dot{
        width: 10px;
        height: 10px;
        border-radius: 5px;
        background-color:#fff;
        border: none;
        padding: 0px;
        display: inline-block;
        margin-right: 14px;
        position: relative;
        &.active{
          span{
            width: 8px;
            height: 8px;
            background-color:#0e569e;
            display: block;
            position: absolute;
            left: 50%;
            top: 50%;
            margin-top: -4px;
            margin-left: -4px;
            border-radius: 4px;
          }
        }
      }
    }
  }
  .place-block{
    position: relative;
    &:before{
      content:'';
      width: 40px;
      height: 21px;
      background-image: url(../img/arrow3.png);
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;
      position: absolute;
      left: 50%;
      margin-left: -20px;
      bottom:-20px;
      z-index: 10;
    }
    .inner{
      background-color:#0e569e;
      padding-top: 80px;
    padding-bottom: 67px;
      .flex{
        display: flex;
        justify-content: space-between;
        max-width: 840px;
        margin: 0 auto;
        .item{
          .icon{
            background-image:url(../img/icons3.png);
            background-repeat:no-repeat;
            margin-bottom: 25px;
            height: 108px;
          }
          a{
            text-align: center;
            display: block;
            font-size: 18px;
            font-weight: bold;
            color:#fff;
            &:hover{
              color:#ff6600;
            }
          }
          &:nth-child(1){
            .icon{
              width: 120px;
              background-position:top left;
            }
          }
          &:nth-child(2){
            .icon{
              width: 115px;
              background-position:34% 3px;
            }
          }
          &:nth-child(3){
            .icon{
              width: 118px;
              background-position:68.1% 3px;
            }
          }
          &:nth-child(4){
            .icon{
              width: 87px;
              background-position:100% 3px;
            }
          }
        }
      }
    }
  }
  .partners-block{
    padding-bottom: 65px;
    .inner2{
      display: block;
      max-width: 940px;
    }
    h2{
      font-size: 32px;
      text-transform: uppercase;
      display: block;
      text-align: center;
      margin: 0 auto;
      color:#0e569e;
      margin-top: 70px;
      margin-bottom: 10px;
    }
    .flex{
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      .logo{
        background-image:url(../img/logos.png);
        background-repeat:no-repeat;
        height: 80px;
        margin-left: 20px;
        margin-right: 20px;
        margin-top: 30px;
        &:nth-child(1){
          width: 101px;
          background-position:-16px 0%;
        }
        &:nth-child(2){
          width: 127px;
          background-position:-167px 0%;
        }
        &:nth-child(3){
          width: 114px;
          background-position:-338px 0%;
        }
        &:nth-child(4){
          width: 128px;
          background-position:-491px 0%;
        }
        &:nth-child(5){
          width: 124px;
          background-position:-657px 0%;
        }
        &:nth-child(6){
          width: 83px;
          background-position:-850px 0%;
        }
        &:nth-child(7){
          width: 108px;
          background-position:-16px -98px;
        }
        &:nth-child(8){
          width: 88px;
          background-position:-188px -98px;
        }
        &:nth-child(9){
          width: 99px;
          background-position:-342px -98px;
        }
        &:nth-child(10){
          width: 97px;
          background-position:-508px -98px;
        }
        &:nth-child(11){
          width: 142px;
          background-position:-649px -98px;
        }
        &:nth-child(12){
          width: 122px;
          background-position:-821px -92px;
        }
        &:nth-child(13){
          width: 127px;
          background-position:-3px -196px;
        }
        &:nth-child(14){
          width: 131px;
          background-position:-165px -196px;
        }
        &:nth-child(15){
          width: 124px;
          background-position:-657px 0%;
        }
        &:nth-child(16){
          width: 133px;
          background-position:-491px -196px;
        }
        &:nth-child(17){
          width: 99px;
          background-position:-342px -98px;
        }
        &:nth-child(18){
          width: 83px;
          background-position:-850px 0%;
        }
      }
    }
  }
  .link-block{
    .inner{
      background-color:#F7F7F7;
      padding-top: 71px;
    }
    .name{
      span{
        display: block;
        text-transform: uppercase;
        font-size: 32px;
        text-align: center;
        &:nth-child(1){
          color: #0e569e;
        }
        &:nth-child(2){
          color: #FF6600;
        }
      }
    }
    .flex{
      display: flex;
      justify-content: space-between;
      max-width: 890px;
      width: 98%;
      margin: 0 auto;
      margin-top: 35px;
      .item{
        width: 25%;
        p{
          font-weight: bold;
          font-size: 18px;
          margin-top: 20px;
          margin-bottom: 15px;
          display: block;
          text-align: center;
        }
        span{
          text-align: center;
          display: block;
          padding: 0px 15%;
        }
        a{
          display: block;
          text-align: center;
          &.fon{
            font-size: 20px;
            color: #333;
          }
          &.mail{
            color:#0e569e;
          }
        }
        .icon{
          background-image:url(../img/icons4.png);
          height: 58px;
          background-repeat:no-repeat;
          margin: 0 auto;
        }
        &:nth-child(1){
          .icon{
            width: 50px;
          }
        }
        &:nth-child(2){
          .icon{
            width: 46px;
            background-position:-246px 0px;
          }
        }
        &:nth-child(3){
          .icon{
            width: 46px;
            background-position:-492px 0px;
          }
        }
        &:nth-child(4){
          .icon{
            width: 58px;
            background-position:-732px 0px;
          }
        }
      }
    }
    .map-block{
      height: 500px;
      margin-top: 37px;
    }
  }
  .form-block{
    .inner{
      padding-top: 53px;
      padding-bottom: 66px;
      background-color:#0e569e;
    }
    .name{
      span{
        display: block;
        font-size: 32px;
        text-align: center;
        &:nth-child(1){
          color: #fff;
        }
        &:nth-child(2){
          color: #FF6600;
        }
      }
    }
    .flex{
      display: flex;
      max-width: 980px;
      margin: 0 auto;
      justify-content: space-between;
      margin-top: 39px;
      form{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        width: 100%;
        max-width: 780px;
        div{
          input,textarea{
            width: 100%;
            padding: 15px 20px;
            margin: 0;
            display: block;
            background-color:#1B67B3;
            border:none;
            box-sizing:border-box;
            color: #fff;
            font-size: 16px;
            &:focus{
              background-color:#4185C9;
            }
          }
          &:nth-child(3){
            height: 115px;
            width: 100%;
            display: block;
            margin-top: 20px;
            textarea{
              height: 100%;
              resize: none;
            }
          }
          &:nth-child(1){
            width: calc(50% - 10px);
          }
          &:nth-child(2){
            width: calc(50% - 10px);
          }
        }
      }
      a{
        align-self: flex-end;
      }
    }
  }

  .questions{
    margin-top: 58px;
  }
}


@media screen and (max-width:1240px){
  .index-page .services {
    .inner{
      padding: 0;
    }
    .flex {
    justify-content: space-around;
    .item{
      margin-right: 0;
      width: 280px;
    }
  }
}
.index-page .about_company .inner{
  padding: 0;
}
.index-page .about_company .flex{
  display: grid;
  .item:nth-child(1){
    order: 0;
  }
  .item:nth-child(2){
    order: 1;
  }
  .item:nth-child(3){
    order: 3;
  }
  .item:nth-child(4){
    order: 2;
  }
  .item:nth-child(5){
    order: 4;
  }
  .item:nth-child(6){
    order: 5;
  }
  .item:nth-child(7){
    order: 7;
  }
  .item:nth-child(8){
    order: 6;
  }
}
.index-page .about_company .flex .item{
  width: auto;
  height: auto;
  margin-top: 10px;
}
.index-page .about_company .flex .item.image{
  width: 460px;
  height: 300px;
}
.index-page .description{
  margin-top: 20px;
}
}
@media screen and (max-width:1020px){
  .index-page .banner_block-2 .item .left-block{
    margin-top: 50px;
    margin-bottom: 50px;
    padding: 20px;
  }
  .index-page .banner_block-2 .item .right-block a{
    margin-right: 20px;
    margin-bottom: 50px;
    margin-left: 20px;
  }
  .index-page .questions .flex:nth-child(2){
    flex-direction: column;
    justify-content: center;
    align-items:center;
    >div{
      margin-top: 10px;
    }
  }
  .index-page .place-block .inner .flex{
    .item{
      width: 25%;
      .icon{
        margin: 0 auto;
        margin-bottom: 10px;
      }
    }
  }
  .index-page .partners-block .flex{
    justify-content: center;
  }
  .index-page .form-block .inner{
    padding: 20px 0px;
  }
  .index-page .form-block .flex{
    margin-top: 10px;
  }
  .index-page .form-block .flex {
    flex-direction: column;
    align-items:center;
    justify-content: center;
    a{
      align-self: center;
      margin-top: 20px;
    }
  }
}
@media screen and (max-width:880px){
  .index-page .banner-block .wight-block{
    width: 100%;
    &:before{
      display: none;
    }
  }
  .index-page .questions .flex{
    flex-direction: column;
    align-items:center;
  }
  .index-page .questions .inner{
    padding-top: 20px;
    padding-bottom: 20px;
  }
  .index-page .questions .flex .left, .index-page .questions .flex .right{
    width: auto;
    margin-bottom: 10px;
    text-align: center;
  }
  .index-page .description .flex{
    flex-wrap: wrap;
    .item{
      margin-top: 10px;
      width: 300px;
